'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function NewsPage() {
  const [news, setNews] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);

  const discoverNews = async () => {
    setLoading(true);
    try {
      const res = await fetch('http://localhost:8000/api/news/discover');
      const data = await res.json();
      const newsItems = data.news.split('\n').filter((n: string) => n.trim());
      setNews(newsItems);
    } catch (error) {
      alert('ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£');
    }
    setLoading(false);
  };

  const createArticle = async (newsTitle: string) => {
    try {
      const res = await fetch(`http://localhost:8000/api/news/create/${encodeURIComponent(newsTitle)}`, {
        method: 'POST'
      });
      const article = await res.json();
      window.location.href = `/article/${article.id}`;
    } catch (error) {
      alert('ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£');
    }
  };

  return (
    <div className="min-h-screen bg-slate-50">
      <nav className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-4 flex justify-between items-center">
          <h1 className="text-2xl font-bold text-slate-800">ÿßŸÉÿ™ÿ¥ŸÅ ÿ£ÿÆÿ®ÿßÿ± ÿ™ŸÇŸÜŸäÿ©</h1>
          <Link href="/" className="text-blue-600 hover:text-blue-700">‚Üê ÿßŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ©</Link>
        </div>
      </nav>

      <main className="max-w-4xl mx-auto px-4 py-8">
        <button
          onClick={discoverNews}
          disabled={loading}
          className="w-full px-8 py-4 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors font-bold text-lg shadow-sm disabled:opacity-50"
        >
          {loading ? '‚è≥ ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ®ÿ≠ÿ´...' : 'üîç ÿßŸÉÿ™ÿ¥ŸÅ ÿ£ÿ≠ÿØÿ´ ÿßŸÑÿ£ÿÆÿ®ÿßÿ± ÿßŸÑÿ™ŸÇŸÜŸäÿ©'}
        </button>

        <div className="mt-8 space-y-4">
          {news.map((item, i) => (
            <div key={i} className="bg-white rounded-lg shadow-sm border p-4 hover:shadow-md transition-shadow">
              <p className="text-gray-800 mb-3">{item}</p>
              <button
                onClick={() => createArticle(item)}
                className="text-sm bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
              >
                ‚úçÔ∏è ÿµŸäÿßÿ∫ÿ© ÿßŸÑÿÆÿ®ÿ±
              </button>
            </div>
          ))}
        </div>
      </main>
    </div>
  );
}
